 fraction of a second, the model predictions made for these tasks provide a series of categorizations for an individual report. We leverage state-of-the-art CNNs, which strike a necessary balance between model complexity, memory and storage constraints, and model performance to provide these predictions. "),_c('br'),_c('br'),_vm._v(" To achieve this aim, we use large, labeled, open-source datasets for training and evaluating the models. We formulate a new crisis image classification task for detecting flood presence in an image and construct a new dataset altogether using flood images from various open-source datasets, which contained flood-adjacent labels. We additionally devise an annotation procedure and analyze the results of human-annotations on crisis images provided to us by crisis managers in Fukuchiyama. We held various image annotation workshops with crisis managers to identify the limitations in our approach and understand how we could iterate on the design of our ML methodology. This evaluation procedure thus enabled us to evaluate the use of image classification models in assisting crisis managers using quantitative metrics as well as qualitatively through the feedback we got through the image annotation workshops. This evaluation directly influenced our approach for devising the Text Analysis Module. ")])])])]),_c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"carousel-text"},[_c('h5',[_vm._v("Image Datasets & Train/Dev/Test Splits for Model Development & Evaluation")]),_c('div',{staticClass:"row justify-content-around"},[_c('div',{staticClass:"carousel-text col-9"},[_c('p',[_vm._v(" Since we used CNN models, specifically the EfficientNet-B1 architecture pretrained on ImageNet, we wanted to make use of large open-source image datasets for finetuning the models to perform the classification tasks of Damage Severity (DS), Humanitarian Categories (HC), Informativeness (IN), and Flood Presence (FP). ")]),_c('h6',[_vm._v("Open-